import shopItem from "../../components/shopItem";
import { get,post } from "../../libs/request.js"

export default {
  data() {
    return {
		latitude:null,
		longitude:null,
		shopData:[]
	};
  },
  onLoad(e) {
	  this.latitude = e.latitude || '';
	  this.longitude = e.longitude || '';
  },
  onShow(){
	  this.getNearData();
  },
  components: {
    shopItem
  },
  methods:{
	  getNearData(){
		  post('shop/nearby',{
			"lat": this.latitude,
			"lng": this.longitude,
			"range":2000
		  }).then(res=>{
			  if(res.code === 200){
				  this.shopData = res.data;
			  }
		  })
	  }
  }
};
